.main {
  height: 100vh;
  width: 100vw;
}

body {
  font-family: 'Century Gothic', sans-serif;
  background-color: #f8f8f8;
  color: #666666;
  font-size: 0.8rem;
  
overflow-x: hidden;
}

.sidebar {

  width: 100%;
  height: 100vh;
  left: 0px;
  top: 0px;

  /* grey */

  background: white;
  border: 1px solid #E5E5E5;
  box-sizing: border-box;
}
.iconbox {
  background: #FFFFFF;
  backdrop-filter: blur(17.2611px);
  border: 0.3px solid;
  border-radius: 100px;
  border-color: transparent;
  color: #666666;
}

.iconbox:hover {
  background-color: #666666;
  color: white;
}
.bell,
.search,
.pic {
  width: 35px;
  height: 30px;
}

.btn-group-sm>.btn,
.button {
  line-height: 0.5px;
  height: 25px;
  width: 60px;
}
.form-select {
  border-color: #CACACA;
  border-width: 1px;
  width: 10rem;
  
  height: 45%;
  border-radius: 0%;
}

.border,
.btn {
  border-color: #CACACA;
  border-width: 1px;
  border-radius: 0;
}

.nameselect {
  width: 5rem;
  height: 35%;
  border-radius: 0%;
}


.card {
  border-radius: 0%;
}

.field {
  height: 80%;
  width: 93%;
  border-radius: 0%;

}
/*
.label>label {
  font-size: 0.8rem;
}
*/
.btn-outline-secondary.focus,
.btn-outline-secondary:focus {
  box-shadow: 0 0 0 0.2rem rgb(108 117 125 / 0%);
}

#verticalbar a:hover {
  background-color: #666666;
  color: #E5E5E5;
  text-decoration: none;
}

#verticalbar .box {
  background-color: #E5E5E5;
  color: #666666;
}


.form-control {
  font-size: 0.8rem;
  padding: 0.1rem 0.2rem;
}

label {
  margin-bottom: 0.3rem;
}

.pagination .page-link {
  color: #666666;
}

#accordion .card-header,
.card {
  border-bottom: transparent;
  border: transparent;
}

.pad {
  padding: 0.15rem;
}

dl,
ol,
ul {
  margin-top: 0;
  margin-bottom: 0rem;
}

a{
  color: #666666;
}
a:hover{
  color: #333333;
}